function arrayCategories (categories) {
  var array = [];
  var headItem = null;
  categories.forEach(type => {
    if(type.series_code % 100 === 0) {
      if( headItem ) {
        array.push(headItem);
      }
      headItem = type;
      headItem.children = [];
    } else {
      headItem.children.push(type);
    }
  });
  array.push(headItem);
  return array;
}

const Categories = arrayCategories(Serieses);
export default Categories;